$primary-bg: #f4f4f4;
$hover-bg: #f1f1f1;
$border-color: #ddd;

body {
    font-family: Arial, sans-serif;
    margin: 20px;

    h1 {
        margin-bottom: 20px;
    }
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;

    thead {
        th {
            background-color: $primary-bg;
            border: 1px solid $border-color;
            padding: 10px;
            text-align: left;
        }
    }

    tbody {
        tr {
            &:hover {
                background-color: $hover-bg;
            }

            td {
                border: 1px solid $border-color;
                padding: 8px;
                
            }
            
        }
    }
}
.td-wrap-text{
    width: 30%;
                word-wrap: break-word; 
                overflow-wrap: break-word; 
                white-space: normal;
            }