* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
  }
  .header-container{
    display: flex;
    align-items:center;
  }
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    // background-color: #333;
    padding: 10px 20px;
  
    &-brand {
      color: rgb(10, 10, 10);
      font-size: 1.5em;
    }
  
    &-menu {
      list-style: none;
      display: flex;
  
      li {
        margin: 0 15px;
  
        a {
          color: rgb(0, 0, 0);
          text-decoration: none;
          transition: color 0.3s;
  
          &:hover {
            color: #ff6347; // Color al pasar el mouse
          }
        }
      }
    }
  
    &-toggle {
      display: none;
      font-size: 1.5em;
      color: rgb(66, 66, 66);
    }
  
    // Media Queries
    @media (max-width: 768px) {
      &-menu {
        display: none; // Oculta el menú en móviles
        flex-direction: column;
        position: absolute;
        background-color: #333;
        top: 60px; // Alineación debajo de la navbar
        right: 0;
        width: 100%;
  
        &.active {
          display: flex; // Muestra el menú cuando está activo
        }
      }
  
      &-toggle {
        display: block; // Muestra el ícono de menú
      }
    }
  }
  