.main-container{
    display: flex;
    nav{
        display: block;
        width: 15%;
        .navbar-menu{
            display: block;
            width: 100%;
            li{
                
                margin: 5px 0px;
                padding: 10px;
                border-radius: 8px;
                background-color: rgb(1, 102, 18);
                color: rgb(226, 226, 226);
                border-width: 1px;
                border-style: solid;
                
                &:hover{
                    background-color: rgb(255, 255, 255);
                    color: black;
                    border-width: 1px;
                    border-style: solid;
                    border-color: rgb(1, 102, 18);
                }
            }
        }
    }
    .main-body-container{
        max-width: 85%;
    }
}